    <h4 class="fw-bold"> Jedyna taka brygada</h4>

    <p class="fw-bold">
      Po klęsce Francji w końcu czerwca 1940 roku Brytyjczycy spodziewali się, że z Syrii do kontrolowanej przez nich
      Palestyny przejdzie tysiące francuskich żołnierzy oraz garstka Polaków. Jakie było ich zdumienie, gdy zobaczyli,
      że jest akurat odwrotnie. Oto w pełnym porządku i z bronią przybyła im na pomoc Brygada Strzelców Karpackich.
      Jako początek istnienia Brygady Strzelców Karpackich podaje się rozkaz jej sformowania, który Naczelny Wódz
      generał Władysław Sikorski ogłosił 12 kwietnia 1940 roku. Faktem jest jednak, że o tworzeniu polskich oddziałów na
      Środkowym Wschodzie przy tamtejszej armii francuskiej myślano w sztabie Naczelnego Wodza już od początku tego
      roku. W styczniu nakazano attaché wojskowym w Rumunii i na Węgrzech opracowanie planu ewakuacji polskich
      wojskowych na tereny Lewantu (plan nazwano oficjalnie Ewakuacją B), jak nazywano wówczas afrykańskie wybrzeża
      Morza Śródziemnego.
      Już dwa dni po wydaniu rozkazu o organizacji polskiej brygady przy Armii Lewantu generała Weyganda, 14 kwietnia do
      Bejrutu odpłynęła kadra organizacyjna i instruktorska – w sumie 32 oficerów i 20 szeregowców. Pod koniec tego
      miesiąca samolotem do stolicy Libanu dotarł wyznaczony na dowódcę brygady pułkownik dyplomowany Stanisław
      Kopański. Według etatów, Brygada Strzelców Karpackich miała liczyć blisko siedem tysięcy żołnierzy i mieć
      charakter jednostki górskiej. Przymiotnik „Karpacka” w nazwie miał określać kierunek, z jakiego nadeszłaby
      wyzwalać ojczyznę, ale jednocześnie dobrze oddawał doświadczenia wielu jej żołnierzy, którzy przez Karpaty
      zmuszeni byli się przedzierać, by do niej dotrzeć.
    </p>
    <h4 class="fw-bold"> Inteligencka brygada</h4>
    <p>
      Szybko też do obozu wojskowego niedaleko syryjskiego miasta Homs zaczęli zgłaszać się ochotnicy. Byli to w
      większości ludzie nie tylko zahartowani przez dramatyczne przeżycia ostatnich miesięcy, ale i wykształceni.
      Brygada miała największy odsetek żołnierzy z wyższym wykształceniem w całej armii polskiej, a przy tym z
      doświadczeniem wojskowym. Jak podkreślano w oficjalnej historii jednostki: „W szeregach brygady znaleźli się nie
      tylko żołnierze wrześniowi, ale i pewna liczba takich, którzy mieli za sobą kampanię francuską i norweską, a nawet
      egzotyczne walki w Afryce Wschodniej i na Dalekim Wschodzie w oddziałach Legii Cudzoziemskiej”.
      Tacy żołnierze chcieli jak najszybciej ruszyć do walki z Niemcami, ale francuski sojusznik zupełnie ich pod tym
      względem rozczarował. Kiedy do Syrii dotarły wieści o kapitulacji Francji 19 czerwca 1940 roku, dowództwo Armii
      Lewantu opowiedziało się po stronie godzącego się na kolaborację z Niemcami marszałka Pétaina. Wobec groźby
      rozbrojenia i internowania polskiej brygady, generał brygady Stanisław Kopański (awansowany do tego stopnia 3
      maja) postanowił działać zdecydowanie i za wszelką cenę przedostać się do kontrolowanej przez Brytyjczyków
      Palestyny. Na szczęście szef sztabu Armii Lewantu, pułkownik de Larminat okazał się lojalny wobec sojuszników i na
      własną odpowiedzialność wyposażył brygadę w broń, amunicję i sprzęt transportowy, między innymi w ponad 100
      samochodów ciężarowych. Tak wyposażona, polska brygada wywołała sensację pośród brytyjskich żołnierzy, witających
      ją w Palestynie.
    </p>

    <h4 class="fw-bold">Szczury Tobruku</h4>
    <p>
      Polscy żołnierze skierowani zostali do obozu w Latrun, gdzie wyposażono ich w brytyjski ekwipunek i broń. W
      porównaniu z francuskimi niedostatkami i bałaganem, brytyjskie kwatermistrzostwo stanowiło dla Polaków miły
      kontrast. W październiku 1940 roku część brygady przewieziono do egipskiej Aleksandrii, gdzie wykorzystaną ją do
      budowy umocnień i pilnowania obozów jenieckich z wziętymi do niewoli Włochami. Brytyjczycy z wysłaniem brygady na
      front mieli początkowo problem, gdyż Polska nie była w stanie wojny z Włochami, ale kiedy na pustyni pojawiły się
      czołgi Afrika Korps generała Erwina Rommla przyszłego „Lisa Pustyni”, te wątpliwości straciły na znaczeniu.
      12 stycznia 1941 roku zakończono reorganizację brygady według wzorów brytyjskich i odtąd nosiła ona nazwę
      Samodzielnej Brygady Strzelców Karpackich. Wobec niemieckiej inwazji na Grecję wiosną 1941 roku, zdecydowano, by
      ją przerzucić na tamtejszy front. Rozpoczęło się szkolenie górskie i przezbrajanie w sprzęt oddziałów górskich,
      ale nim je zakończono, Grecja padła. Jednocześnie na froncie afrykańskim generał Rommel rozpoczął swą ofensywę,
      więc brygada szybko została przerzucona do twierdzy Marsa Matruh, a następnie ufortyfikowanego Sidi Baggush. Tu
      Polacy zetknęli się z tak egzotycznymi dla nich żołnierzami, jak Południowoafrykańczycy, Australijczycy, Hindusi,
      Nowozelandczycy, żołnierze Legii Cudzoziemskiej Wolnych Francuzów – wszystkich nacji imperium brytyjskiego i jego
      sojuszników w Afryce.
      To była wielka konkurencja w zdobyciu sławy wojennej, tym bardziej że żołnierze południowoafrykańscy czy
      australijscy, nie mówiąc o Legii Cudzoziemskiej, mieli znacznie większe doświadczenie w walkach pustynnych. A
      jednak kiedy karpatczycy pod koniec sierpnia 1941 roku zasilili załogę Tobruk udowodnili, że są wyjątkowo twardym
      i bojowym przeciwnikiem dla oblegających tę kluczową w Libii twierdzę Niemców i Włochów. Na początku października
      brygadzie przypadł najcięższy odcinek obrony – tzw. wyłom w rejonie wzgórza Ras el Medauar. Nieprzyjaciel zajmował
      świetne pozycje strzeleckie na wzgórzu, a odległość jaka dzieliła walczących to było średnio 800, a gdzieniegdzie
      300–200 metrów! Tak pisze o tym w swych wspomnieniach generał Kopański: „… dowódca twierdzy zapytał mnie, jak
      długo mogliby żołnierze Brygady Karpackiej pozostawać na wyłomie, gdyż wobec prawdopodobieństwa wyruszenia
      ofensywy 8 Armii w początku listopada, trudno mu będzie dokonywać przegrupowań […]. Odpowiedziałem mu, że rekord
      pozostawania na wyłomie, osiągnięty przez Australijczyków wynosi cztery tygodnie; że Polacy wytrzymać mogliby o
      tydzień dłużej […]. Nie mogłem wówczas nawet przypuszczać, że skutkiem odłożenia terminu rozpoczęcia ofensywy, a
      następnie jej wolnego posuwania się żołnierze karpaccy ustalą rekord dziesięciotygodniowego pobytu na wyłomie!
      Nikt też z nich na pewno Medauaru do końca życia nie zapomni”.
    </p>

    <h4 class="fw-bold">Elita 2 Korpusu</h4>
    <p>
      W Tobruku karpatczycy swymi bojowymi patrolami i ułańską fantazją (co nie jest wyłącznie przenośnią, gdyż
      szczególnie odznaczyli się tam właśnie żołnierze Pułku Ułanów Karpackich) w pełni zasłużyli na zaszczytne miano
      Szczurów Tobruku, ale po odblokowaniu twierdzy w grudniu 1941 roku przez wspomniane wyżej natarcie brytyjskiej 8
      Armii czekały ich nowe zadania. Spora część polskiej brygady (bez ułanów karpackich) weszła w skład 13 Korpusu 8
      Armii i znowuż się odznaczyła, przełamując linie obronne nieprzyjaciela pod Gazalą. Dzięki temu 8 Armia mogła
      ruszyć do szerokiej ofensywy i wyprzeć Niemców z Cyrenajki.
      Od Nowego 1942 Roku poszczególne oddziały brygady karpackiej wspierały Brytyjczyków w ich ciężkich walkach z
      nieustępliwym Rommlem. Brygadowa artyleria kruszyła umocnienia twierdzy w Bardii. Jej piechurzy wraz z Wolnymi
      Francuzami bronili fortu El Mekili, a później znowu znaleźli się w Gazali i trzymali dzielnie pozycje pod Carmuset
      et Regem. Wreszcie przyszedł czas na odpoczynek i reorganizację. W marcu 1942 roku brygadę wycofano z linii i
      skierowano najpierw do obozu El Amiriya, a następnie do Palestyny. Związane to było też z tym, że w międzyczasie
      trwała już ewakuacja Armii generała Andersa ze Związku Sowieckiego.
      W maju 1942 roku Samodzielna Brygada Strzelców Karpackich została rozformowana, a jej kadra stała się trzonem 3
      Dywizji Strzelców Karpackich Armii Polskiej na Wschodzie, a następnie 2 Korpusu Polskiego. Żołnierze, którzy mieli
      za sobą walki na pustyni, zwani byli przez towarzyszy broni przybyłych z Sowietów ramzesami. Początkowo widoczne
      były pewne antagonizmy między ramzesami i prawosławnymi (lub buzułukami, jak nazywano żołnierzy polskiej Armii w
      ZSRS). Oskarżano ramzesów, że wywyższają się i chwalą sławą wojenną zdobytą w Tobruku i pod Gazalą. Lecz to
      wszystko minęło, kiedy 3 Dywizja Strzelców Karpackich znalazła się we Włoszech na koniec 1943 roku. Na froncie
      włoskim karpatczycy mieli aż nadto okazji, by potwierdzić swą wartość bojową i zdobyć kolejne laury w walce.
    </p>

